body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-strong {
  font-weight: bolder;
  font-size: 22px;
}

.margin-left {
  margin-left: 20px;
}

.project-image {
  display: block;
  height: auto;
}

.header-margin-right {
  margin-right: 40px;
}

.center-sites {
  text-align: center;
  vertical-align: bottom;
  font-weight: bolder;
  font-size: 25px;
}

.main-image {
  display: block;
  margin: 50px;
  max-width: 20%;
  height: auto;
  border-radius: 50%;
  box-shadow: 20px 10px 7px #eef3fc;
  /* #3273dc; */
}

.white-text {
  color:white;
}
